import { Pipe, PipeTransform } from '@angular/core';
import _ from 'lodash';

@Pipe({
  name: 'filterNews',
  pure: false
})
export class FilterNewsPipe implements PipeTransform {
  transform(newsList: any[], filterQuery: String): any {
    if (filterQuery === '') {
      return newsList;
    }

    return newsList.filter(newsline => _.toLower(newsline.title).indexOf(_.toLower(filterQuery)) !== -1);
  }
}
