import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { URL, API_KEY } from './../constants/http.constants';

@Injectable()
export class HttpRequestService {

  constructor(private http: Http) {}

  getHeadlinesIn(category: String) {
    return this.http.get(`${URL}/top-headlines?country=us&category=${category}&apiKey=${API_KEY}`);
  }

  getNewsBySource(source: String) {
    return this.http.get(`${URL}/top-headlines?sources=${source}&apiKey=${API_KEY}`);
  }

  getNewsByQuery(query: String) {
    return this.http.get(`${URL}/top-headlines?q=${query}&apiKey=${API_KEY}`);
  }
}
