import { Component } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import _ from 'lodash';

import { HttpRequestService } from '../../services/news.service';
import { SOURCES } from './../../constants/sources.constants';
import { CATEGORIES } from './../../constants/categories.constants';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent {
  newsList: any;
  sources: Object[];
  categories: Object[];
  source: any;
  searchQuery: String;
  subscriber: any;

  isSearchExpanded: Boolean;
  isSourcesExpanded: Boolean;
  isCategoriesExpanded: Boolean;

  constructor(private httpClient: HttpRequestService,
              private activatedRoute: ActivatedRoute,
              private router: Router) {
    this.categories = CATEGORIES;
    this.sources = SOURCES;

    this.searchQuery = '';

    this.isSearchExpanded = false;
    this.isSourcesExpanded = false;
    this.isCategoriesExpanded = false;
  }

  ngOnInit() {
    this.subscriber = this.activatedRoute.params.subscribe(params => {
      this.newsList = [];
      console.log('onInit');

      if (_.find(this.sources, {id: params['source']})) {
        this.isSourcesExpanded = true;
        this.source = _.find(this.sources, {id: params['source']});

        this.httpClient.getNewsBySource(this.source.id)
          .subscribe(result => {
            this.newsList = result.json().articles;
          });
      }

      if (_.find(this.categories, {id: params['category']})) {
        this.isCategoriesExpanded = true;
        this.source = _.find(this.categories, {id: params['category']});

        this.httpClient.getHeadlinesIn(this.source.id)
          .subscribe(result => {
            this.newsList = result.json().articles;
          });
      }

      if (params['query']) {
        this.isSearchExpanded = true;
        this.source = {name: params['query']};
        this.searchQuery = this.source.name;

        this.httpClient.getNewsByQuery(this.source.name)
          .subscribe(result => {
            this.newsList = result.json().articles;
          });
      }
    });
  }

  expandCollapseBlock(blockExpanded) {
    blockExpanded = !blockExpanded;
    return blockExpanded;
  }

  navigateToSearch() {
    this.router.navigate(['/news/search', this.searchQuery, 'page', 1]);
  }
}
